@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
body {
  background-color: #e9e9e9;
  font-family: "Roboto Condensed", sans-serif; }
  body ol,
  body ul,
  body dl {
    padding-left: 1.5rem; }
  body .header {
    color: #fff;
    background-color: #41b3cd; }
    body .header .top-area {
      padding: 20px 0;
      background-color: rgba(0, 0, 0, 0.1); }
      body .header .top-area h1 {
        font-family: "Permanent Marker", cursive; }
      body .header .top-area .contact-information {
        line-height: 1.2; }
        body .header .top-area .contact-information div:first-of-type {
          font-size: 14px; }
        body .header .top-area .contact-information div:last-of-type {
          font-size: 22px;
          font-weight: 300; }
    body .header .bottom-area {
      padding: 10em 0; }
  body .person-box {
    position: relative;
    z-index: 2;
    color: #fff;
    margin-top: -14.5em;
    padding: 4.5em 0;
    text-align: center; }
    body .person-box::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background-color: rgba(0, 0, 0, 0.75); }
    body .person-box::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -2;
      background: url(/static/media/profile-bg.7ac00407.jpg);
      background-repeat: no-repeat;
      background-size: 175%;
      background-position: left center; }
    body .person-box b {
      font-size: 18px; }
    body .person-box img {
      max-width: 150px;
      border-radius: 50%;
      border: 5px solid #fff; }
    body .person-box a {
      color: #fff;
      display: inline-block;
      padding-top: 20px;
      padding-right: 20px; }
      body .person-box a i {
        font-size: 35px; }
      body .person-box a:last-of-type {
        padding-right: 0; }
  body [class*="custom-btn"] {
    color: #fff;
    padding: 10px 15px;
    border: none;
    position: relative; }
    body [class*="custom-btn"]:hover {
      z-index: 0; }
      body [class*="custom-btn"]:hover::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.1);
        z-index: -1; }
    body [class*="custom-btn"]:active {
      position: relative;
      z-index: 0; }
      body [class*="custom-btn"]:active::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.3);
        z-index: -1; }
    body [class*="custom-btn"].lg {
      padding: 15px 20px;
      font-size: 18px; }
  body .custom-btn-info {
    background-color: #41b3cd; }
  body .custom-btn-light {
    background-color: #fff;
    color: #555; }
  body .custom-btn-primary {
    background-color: #3aa1b8; }
  body .ek-shadow {
    box-shadow: 0px 1px 5px 0px #999999; }
  body .timeline {
    max-width: 1024px; }
    body .timeline .col-sm-5:nth-of-type(3n-2) {
      margin-top: 3rem; }
      body .timeline .col-sm-5:nth-of-type(3n-2) > .card.custom-card {
        position: relative;
        z-index: 1; }
        body .timeline .col-sm-5:nth-of-type(3n-2) > .card.custom-card::after {
          content: "";
          position: absolute;
          right: -30px;
          top: 10px;
          border: 15px solid #007bff;
          border-right-color: transparent;
          border-top-color: transparent;
          border-bottom-color: transparent; }
    body .timeline .col-sm-5:nth-of-type(3n) {
      margin-top: 10rem; }
      body .timeline .col-sm-5:nth-of-type(3n) > .card.custom-card::after {
        content: "";
        position: absolute;
        left: -30px;
        top: 10px;
        border: 15px solid #007bff;
        border-left-color: transparent;
        border-top-color: transparent;
        border-bottom-color: transparent; }
    body .timeline .timeline-dots {
      left: 50%;
      width: 1px;
      height: 100%;
      border-right: 3px dotted #007bff; }
      body .timeline .timeline-dots:before {
        content: "";
        position: absolute;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        background-color: #007bff;
        left: -6.5px;
        top: 4rem; }
      body .timeline .timeline-dots:after {
        content: "";
        position: absolute;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        background-color: #007bff;
        left: -6.5px;
        top: 11rem; }

